// js conflux SDK
// import { Conflux } from '../../node_modules/js-conflux-sdk/dist/js-conflux-sdk.umd.min.js'
import {Conflux} from 'js-conflux-sdk'
import CFXCAUC1 from './abi/CFXCAUC1.json'
import abiSponsorWhitelistControl from './abi/SponsorWhitelistControl.json'

// PRCSDK
const conflux = new Conflux({
  url: process.env.VUE_APP_CONFLUX_NODE_RPC,
  defaultGasPrice: 100, // The default gas price of your following transactions
  defaultGas: 1000000, // The default gas of your following transactions
  logger: console,  // console
  // networkId: 1
})

// SDK
// 
export const NFTContract = {
  name: 'CFXCAUC1',
  abi: CFXCAUC1,
  contract: conflux.Contract({
    abi: CFXCAUC1,
    address: process.env.VUE_APP_CONFLUX_COIN_ADDRESS,
  }),
}

export const ContractSponsorWhitelistControl = {
  name: 'SponsorWhitelistControl',
  abi: abiSponsorWhitelistControl,
  contract: conflux.Contract({
    abi: abiSponsorWhitelistControl,
    address: '0x0888000000000000000000000000000000000001',
  }),
}

export default conflux